import { useState, useEffect, useRef, useCallback } from 'react';
import './CustomCursor.css';

function CustomCursor() {
  const [isHovering, setIsHovering] = useState(false);
  const [currentColorIndex, setCurrentColorIndex] = useState(0);
  const cursorRef = useRef(null);
  const ringRef = useRef(null);
  const rafRef = useRef(null);
  const mousePos = useRef({ x: 0, y: 0 });
  const currentPos = useRef({ x: 0, y: 0 });
  
  // 🌈 RESTORED: Beautiful cursor colors that cycle!
  const cursorColors = [
    '#c2a85d', // Default accent
    '#FF6B9D', // Hot Pink
    '#FFE55C', // Bright Gold  
    '#4CAF50', // Vibrant Green
    '#2196F3', // Electric Blue
    '#FF8A65', // Coral Orange
    '#BA68C8', // Purple
    '#FFD700', // Gold
    '#FF4444', // Red
    '#00E676', // Neon Green
    '#00B8FF', // Cyan
  ];

  // 🎨 RESTORED: Color cycling effect
  useEffect(() => {
    let colorInterval;
    
    if (isHovering) {
      colorInterval = setInterval(() => {
        setCurrentColorIndex((prevIndex) => 
          (prevIndex + 1) % cursorColors.length
        );
      }, 200); // Change color every 200ms when hovering
    } else {
      setCurrentColorIndex(0); // Reset to default when not hovering
    }
    
    return () => {
      if (colorInterval) clearInterval(colorInterval);
    };
  }, [isHovering, cursorColors.length]);
  
  // 🚀 OPTIMIZED: Smooth cursor following with easing
  const updateCursor = useCallback(() => {
    if (!cursorRef.current || !ringRef.current) return;
    
    // Smooth easing for buttery movement
    const ease = 0.15;
    currentPos.current.x += (mousePos.current.x - currentPos.current.x) * ease;
    currentPos.current.y += (mousePos.current.y - currentPos.current.y) * ease;
    
    // 🔥 PERFORMANCE: Single transform update using translate3d for GPU
    // 🎯 FIXED: Perfect centering with translate(-50%, -50%)
    const transform = `translate3d(${currentPos.current.x}px, ${currentPos.current.y}px, 0) translate(-50%, -50%)`;
    cursorRef.current.style.transform = transform;
    ringRef.current.style.transform = transform;
    
    rafRef.current = requestAnimationFrame(updateCursor);
  }, []);

  useEffect(() => {
    // 🚀 OPTIMIZED: Throttled mouse tracking
    const updateMousePos = (e) => {
      mousePos.current.x = e.clientX;
      mousePos.current.y = e.clientY;
    };

    const handleMouseEnter = () => setIsHovering(true);
    const handleMouseLeave = () => setIsHovering(false);

    // Start the animation loop
    rafRef.current = requestAnimationFrame(updateCursor);

    // 🎯 PERFORMANCE: Passive event listeners
    document.addEventListener('mousemove', updateMousePos, { passive: true });

    // Track hover states on interactive elements
    const interactiveElements = document.querySelectorAll(
      'a, button, [role="button"], input, textarea, select, .clickable, .name-easter-egg'
    );

    interactiveElements.forEach(el => {
      el.addEventListener('mouseenter', handleMouseEnter, { passive: true });
      el.addEventListener('mouseleave', handleMouseLeave, { passive: true });
    });

    return () => {
      // 🧹 CLEANUP: Cancel animation frame
      if (rafRef.current) {
        cancelAnimationFrame(rafRef.current);
      }
      
      document.removeEventListener('mousemove', updateMousePos);
      interactiveElements.forEach(el => {
        el.removeEventListener('mouseenter', handleMouseEnter);
        el.removeEventListener('mouseleave', handleMouseLeave);
      });
    };
  }, [updateCursor]);

  // Get current color
  const currentColor = cursorColors[currentColorIndex];

  return (
    <>
      {/* 🚀 OPTIMIZED: Main cursor dot with RESTORED color changing */}
      <div 
        ref={cursorRef}
        className={`custom-cursor ${isHovering ? 'hovering' : ''}`}
        style={{
          '--cursor-color': currentColor, // CSS custom property for dynamic color
        }}
      >
        <div className="cursor-inner"></div>
      </div>

      {/* 🚀 OPTIMIZED: Outer ring with RESTORED color changing */}
      <div 
        ref={ringRef}
        className={`custom-cursor-ring ${isHovering ? 'hovering' : ''}`}
        style={{
          '--cursor-color': currentColor, // CSS custom property for dynamic color
        }}
      >
      </div>
    </>
  );
}

export default CustomCursor;
